*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --border-radius: 5px;
  --light-color: #fff;
  --dark-color: #000;
  --gray-color: #545454;
  --modal-gray-color: #8c8c8c;
  --orange-color: #ff6b01;
  --ganre-orange-color: #ff6b08;
  --color-error: #ff001b;
  --vote-light-grey-color: #f7f7f7;
  --shadow: drop-shadow(0px 8px 43px rgba(255, 107, 1, .6));
  --animationTransition: 250ms cubic-bezier(.4, 0, .2, 1);
  --font-family: "Roboto", sans-serif;
  --font-weight-normal: 400;
  --font-weight-media: 500;
  --background-filter-header: rgba(0, 0, 0, .56);
}

.modal-form-registration {
  z-index: 100;
  width: 100%;
  height: 100%;
  background: #333;
  background: -webkit-linear-gradient(to right, #333, #dd1818);
  opacity: 1;
  visibility: visible;
  background: linear-gradient(to right, #333, #dd1818);
  padding: 30px;
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
}

.modal-form-registration.is-hidden {
  opacity: 0;
  visibility: hidden;
  display: none;
}

.title-wrapper {
  max-width: 500px;
  text-align: center;
  flex-direction: column;
  margin-bottom: 120px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.slider-tv {
  max-width: 200px;
  position: relative;
}

.icon-tv {
  fill: #f0f8ff;
  position: absolute;
  top: 0;
  left: 0;
}

.form-swiper {
  position: absolute;
  top: 80px;
  left: 12.5%;
}

.form-movies-img {
  width: 160px;
  height: 100px;
  border-radius: 10px;
}

.modal-form-registration_title {
  color: var(--orange-color);
  margin-bottom: 30px;
}

.modal-form-registration_sub-title {
  color: var(--orange-color);
}

.form-registration {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 15px;
  display: flex;
  position: relative;
}

.man-watch {
  display: none;
  position: absolute;
  bottom: 39%;
  right: 28%;
}

.man-watch.hidden {
  display: block;
}

.icon-watch {
  fill: #f6f4f3;
}

.form-group-registaration {
  flex-direction: column;
  display: flex;
}

.email-form-title, .password-form-title {
  color: var(--orange-color);
  justify-content: start;
  margin-bottom: 5px;
  display: flex;
}

.input-password, .input-email, .forgoten-password {
  width: 300px;
  height: 30px;
  outline: 2px solid var(--gray-color);
  color: var(--light-color);
  border-radius: var(--border-radius);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 10px;
}

.input-password:focus, .input-email:focus, .forgoten-password:focus {
  outline: 2px solid var(--orange-color);
  transition: var(--animationTransition);
}

.password-container {
  position: relative;
}

.icon-open {
  cursor: pointer;
  opacity: 1;
  filter: invert();
  -webkit-filter: invert();
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
}

.icon-close {
  cursor: pointer;
  opacity: 0;
  filter: invert();
  -webkit-filter: invert();
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
}

.icon-close.hide {
  opacity: 1;
}

.forgoten-group-registaration {
  display: none;
}

.forgoten-group-registaration.hidden {
  display: block;
}

.forfgot-password {
  color: var(--light-color);
  transition: color var(--animationTransition);
}

.forfgot-password:hover, .forfgot-password:focus {
  color: var(--orange-color);
  outline: none;
}

.forgotten-label {
  color: var(--orange-color);
  margin-bottom: 5px;
  display: flex;
}

.forgotten-container {
  display: flex;
}

.forgoten-password {
  margin-right: 15px;
}

.btn-new-password {
  z-index: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.form-group-registaration-btn {
  flex-direction: row;
  column-gap: 15px;
  display: flex;
}

.social-form-question {
  color: var(--light-color);
}

.button-sign-group {
  column-gap: 15px;
  display: flex;
}

@media (min-width: 320px) {
  .modal-form-registration_title {
    margin-bottom: 10px;
    font-size: 18px;
  }

  .modal-form-registration_sub-title {
    font-size: 14px;
  }

  .slider-wrapper {
    top: -29%;
    left: 50%;
    transform: perspective(600px)translate(0, -100%)rotateY(0deg);
  }

  .slider-tv {
    max-width: 200px;
  }

  .icon-tv {
    fill: #f0f8ff;
    width: 150px;
    opacity: .4;
    position: absolute;
    top: 0;
    left: 0;
  }

  .form-swiper {
    position: absolute;
    top: 80px;
    left: 6%;
  }

  .form-movies-img {
    width: 127px;
    height: 85px;
    border-radius: 10px;
  }

  .man-watch.hidden {
    display: none;
  }

  .forgotten-label {
    font-size: 12px;
  }

  .forgoten-password {
    width: 250px;
  }
}

@media (min-width: 768px) {
  .slider-wrapper {
    left: 40%;
  }
}

@media (min-width: 1280px) {
  .modal-form-registration_title {
    margin-bottom: 10px;
    font-size: 28px;
  }

  .modal-form-registration_sub-title {
    font-size: 22px;
  }

  .slider-wrapper {
    position: absolute;
    top: 10%;
    left: 23%;
    transform: perspective(600px)translate(0, -100%)rotateY(45deg);
  }

  .man-watch.hidden {
    opacity: .4;
    display: block;
  }
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.lazyload, .lazyloading {
  opacity: 0;
  transition: all 1s;
  transform: translateX(-100%);
}

.lazyloaded {
  opacity: 1;
  transition: transform 1s, opacity 1s;
  transform: translateY(0);
}

.skiptranslate {
  height: 0;
  display: none;
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

body {
  font-family: var(--font-family);
  background: #abbaab;
  background: -webkit-linear-gradient(to right, #fff, #abbaab);
  height: 100%;
  background: linear-gradient(to right, #fff, #abbaab) center / cover no-repeat fixed;
  margin: 0;
  font-size: 14px;
}

body.toggle-theme {
  background: #8e0e00;
  background: -webkit-linear-gradient(to right, #1f1c18, #8e0e00);
  background: linear-gradient(to right, #1f1c18, #8e0e00);
}

body.stop-fon {
  overflow: hidden;
}

.scroll-btn-up {
  position: relative;
}

.scroll-btn {
  z-index: 15;
  height: 45px;
  background-color: var(--orange-color);
  transition: var(--animationTransition);
  opacity: .6;
  border-radius: 50%;
  padding: 10px;
  display: none;
  position: fixed;
  bottom: 40px;
  right: 40px;
}

.scroll-btn:hover {
  opacity: .8;
  transform: scale(1.3);
}

body.toggle-theme .scroll-btn {
  background: #eb5757;
  background: -webkit-linear-gradient(to right, #eb5757, #000);
  background: linear-gradient(to right, #eb5757, #000);
}

.scroll-btn__svg {
  fill: var(--gray-color);
}

.scroll-btn.shown {
  display: block;
}

.btn-form {
  cursor: pointer;
  width: 80px;
  height: 30px;
  border: 2px solid var(--gray-color);
  color: var(--orange-color);
  border-radius: var(--border-radius);
  transition: var(--animationTransition);
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  padding: 5px;
  font-size: 10px;
}

.btn-form:hover, .btn-form:focus {
  background-color: var(--orange-color);
  border: 2px solid var(--orange-color);
  color: var(--light-color);
}

.whirly-loader__wrapper {
  width: 100%;
  height: 64vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.whirly-loader__wrapper.preloader_hidden {
  display: none;
}

