.theme-btn {
  width: 30px;
  background: #2a2c2d;
  background-color: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.theme-btn button {
  color: #000;
  background: #0060df;
  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.switch {
  width: 45px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch:focus {
  outline: none;
  transform: scale(1.1);
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: rgba(204, 204, 204, .3);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 25px;
  width: 25px;
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center / 20px no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: rgba(204, 204, 204, .3);
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center / 16px no-repeat;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
  box-shadow: 0 0 10px 5px rgba(247, 249, 249, .99);
}

body.toggle-theme .slider.round:before {
  box-shadow: 0 0 10px 7px rgba(238, 4, 4, .7);
}

.header {
  background-image: url("sm.57ef78c4.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 10px;
  position: relative;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .header {
    background-image: linear-gradient(to right, var(--background-filter-header), var(--background-filter-header) ), url("sm@2x.2ac8798d.jpg");
  }
}

body.toggle-theme .header {
  box-shadow: 0 3px 5px 5px rgba(255, 255, 255, .1);
}

.logo {
  color: #fffbd5;
}

body.toggle-theme .logo {
  color: brown;
}

.logo-text {
  display: none;
}

.nav-wrapper {
  justify-content: space-between;
  margin-bottom: 30px;
  display: flex;
}

.info-user {
  display: flex;
}

.info-usrer-photo {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 5px;
}

.info-user-name, .info-user-email {
  font-family: var(--font-family);
  font-weight: var(--font-weight-media);
  color: var(--light-color);
  font-size: 8px;
  line-height: 1.25;
}

body.toggle-theme .info-user-name, body.toggle-theme .info-user-email {
  color: brown;
  background: #fffbd5;
  background: -webkit-linear-gradient(to right, #b20a2c, #fffbd5);
  background: linear-gradient(to right, #b20a2c, #fffbd5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.utils-wrapper {
  justify-content: center;
  align-items: center;
  column-gap: 50px;
  margin-bottom: 30px;
  display: flex;
}

.btn-log-out {
  width: 54px;
  height: 24px;
  border-radius: var(--border-radius);
  border: 1px solid var(--light-color);
  cursor: pointer;
  color: var(--light-color);
